<template>
  <div>
    <div
      class="card"
      v-for="hero in champions"
      :key="hero.name"
      v-if="hero.showname === heroName || hero.name === heroName"
      :style="{width: cardSize + 'px', height: cardSize*1.25 + 'px'}"
    >
      <img
        :src="'static/img/champion/' + hero.class + '/' + hero.name + '.png'"
        :style="{ borderColor: hero.color, width: cardSize + 'px', height: cardSize + 'px'}"
      >
      <div
        :style="{ width: cardSize + 'px', height: cardSize*0.25 + 'px', lineHeight: cardSize*0.25 + 'px'}"
      >
        <span style="color: Red" v-if="hero.isAwakened">★</span>
        <span style="color: OrangeRed" v-if="hero.needHSL">❖</span>
        <span style="color: Crimson" v-if="hero.needSynergy">♥</span>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Aq',
  props: {
    championName: String
  },
  data () {
    return {
      heroName: this.championName,
      champions: this.GLOBAL.champions,
      cardSize: 60
    }
  }
}
</script>

<style lang="sass" scoped>
@import ../assets/sass/juice
input
  text-align: center

.card
  display: flex
  flex-direction: column
  align-items: center
  box-shadow: 0px 3px 11px 0px rgba(173,173,173,1)
  box-sizing: border-box
  img
    background: rgb(54,19,1)
    background: linear-gradient(356deg, rgba(54,19,1,1) 0%, rgba(134,97,0,1) 100%)
    border: 3px solid
    box-sizing: border-box
  div
    text-align: center

</style>



