import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/components/Home'
import Aq from '@/components/Aq'
import Attack from '@/components/Attack'
import Group from '@/components/Group'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Home',
      component: Home
    },
    {
      path: '/aq',
      name: 'Aq',
      component: Aq
    },
    {
      path: '/attack',
      name: 'Attack',
      component: Attack
    },
    {
      path: '/group',
      name: 'Group',
      component: Group
    }
  ]
})



